
import React, {useState, useRef, useEffect, memo, FC} from 'react';
import {
    createSmartappDebugger,
    createAssistant,
    AssistantAppState,AssistantCharacterType
} from "@sberdevices/assistant-client";

// createGlobalStyle нужен для создания глобальных стилей
import styled, { createGlobalStyle } from 'styled-components';

// получаем значение для целевой платформы
import { sberBox,  touch } from '@sberdevices/plasma-tokens/typo';
import { darkJoy, darkEva, darkSber } from '@sberdevices/plasma-tokens/themes';
// получаем стилевые объекты для нашего интерфейса
import { body1 } from '@sberdevices/plasma-tokens';

import {sberPortal} from './helpers/css/portalcss'

// получаем цвета для нашего интерфейса
import { text, background, gradient } from '@sberdevices/plasma-tokens';

import background_img from "./images/background.jpg"

import Main from './components/Main'
import UIfx from 'uifx'
import beepMp3 from './Sound/sound 1.mp3'

import img_background_portal_sber from "./images/background_1280x800_s.jpg"
import img_background_portal_ava from "./images/background_1280x800_a.jpg"
import img_background_portal_joy from "./images/background_1280x800_j.jpg"

import img_background_sber from "./images/background_1980x1080_s.jpg"
import img_background_ava from "./images/background_1980x1080_a.jpg"
import img_background_joy from "./images/background_1980x1080_j.jpg"





import { detectDevice } from '@sberdevices/ui/utils';
const ThemeBackgroundEva = createGlobalStyle(darkEva);
const ThemeBackgroundSber = createGlobalStyle(darkSber);
const ThemeBackgroundJoy = createGlobalStyle(darkJoy);
const TypoScaleSberBox = createGlobalStyle(sberBox);
const TypoScaleSberPortal = createGlobalStyle(sberPortal);
const TypoScaleTouch = createGlobalStyle(touch);

const beep = new UIfx(beepMp3)

const AppStyled = styled.div`
  padding: 30px;
  ${body1}
`;



const DocStyle = createGlobalStyle`
  html {
    color: ${text};
    background-color: ${background};
    background-image: ${gradient};
    min-height: 100vh;
  }
  `;

