export const App: FC = memo(() => {
    const [character, setCharacter] = useState("joy" as AssistantCharacterType);
    const assistantStateRef = useRef<AssistantAppState>();
    const assistantRef = useRef<ReturnType<typeof createAssistant>>();

    const [posGame, setPosGame] = useState(1);
    const [command, setCommand] = useState("")
    const [buttonLDown, setButtonLDown] = useState(false);
    const [buttonRDown, setButtonRDown] = useState(false);

    const downHandler = (event:any) => {
        // ArrowDown
        // ArrowUp

        if (event.code === 'ArrowLeft') {
            leftbuttondown()
            setButtonLDown(true)
        }
        if (event.code === 'ArrowRight') {
            rightbuttondown()
            setButtonRDown(true)
        }
    }



    const leftbuttondown = React.useCallback(() => {
        beep.play()
        setPosGame( (prev) => {
            const x = prev>1?-1:0;
            return prev + x;
        })
    }, [])

    const rightbuttondown = React.useCallback(() => {
        beep.play()
        setPosGame( (prev) => {
            const x = prev<4?1:0;
            return prev + x;

        })
    }, [])

    const getcommand = React.useCallback((command) => {

        if (command !== undefined)  setCommand( command)

    }, [])


    const upHandler = (event:any) =>{

        if (event.code === 'ArrowLeft') {

            setButtonLDown(false)
        }
        if (event.code === 'ArrowRight') {
            setButtonRDown(false)
        }
    }


    useEffect(() => {
        window.addEventListener('keydown', downHandler);
        window.addEventListener('keyup', upHandler);
        // Remove event listeners on cleanup
        return () => {
            window.removeEventListener('keydown', downHandler);
            window.removeEventListener('keyup', upHandler);
        };
    }, []); // Empty array ensures that effect is only run on mount and unmount


    useEffect(() => {
        assistantRef.current = initializeAssistant(() => assistantStateRef.current);


        assistantRef.current.on("data", (command) => {

            console.log(`command: ${JSON.stringify(command)}`);

            switch (command.type) {
                case "character":

                    setCharacter(command.character.id);
                    console.log(command.character.id)
                    // 'sber' | 'eva' | 'joy';
                    break;
                case "navigation":
                    switch(command.navigation.command) {
                        case 'LEFT':
                            console.log('LEFT')
                            leftbuttondown()
                            break;
                        case 'RIGHT':
                            console.log('RIGHT')
                            rightbuttondown()
                            break;
                        default:
                    }
                    break;



                case "smart_app_data":

                    // @ts-ignore
                    const  c =  command.smart_add_data.game

                          getcommand(c);
                          break;

                    break;
                default:
                    return;
            }


        });
    }, []);



    return (
        <AppStyled>
            {(() => {
                let device = detectDevice();

                // device = "sberBox" as "sberPortal" | "sberBox" | "touch";
                // is for testing only
                // detectDevice() returns "touch" for local run
                // ... or change user-agent in browser
                //
                if  (device === "touch")
                {
                    device = "sberBox" as "sberPortal" | "sberBox" | "touch";
                }

                switch (device) {
                    case "sberBox":
                        return <TypoScaleSberBox />
                    case "sberPortal":
                        return <TypoScaleSberPortal />
                    case "touch":
                        return <TypoScaleTouch />
                    default:
                        return;
                }
            })()}
            {(() => {
                switch (character) {
                    case "sber":
                        return <ThemeBackgroundSber />;
                    case "eva":
                        return <ThemeBackgroundEva />;
                    case "joy":
                        return <ThemeBackgroundJoy />;
                    default:
                        return;
                }
            })()}
            <DocStyle/>

            <>
                <div style = {{position:"absolute", left:0, top:0}}>
                    {(() => {

                        let device = detectDevice();

                        // device = "sberBox" as "sberPortal" | "sberBox" | "touch";
                        // is for testing only
                        // detectDevice() returns "touch" for local run
                        // ... or change user-agent in browser
                        //
                        if  (device === "touch")
                        {
                            device = "sberBox" as "sberPortal" | "sberBox" | "touch";
                        }


                        if ((device === "sberBox") || (device === "touch")){
                            if (character === "sber")   return <img  src={img_background_sber}/>;
                            if (character === "eva")    return <img  src={img_background_ava}/>;
                            if (character === "joy")    return <img  src={img_background_joy}/>;
                        }

                        if (device === "sberPortal") {
                            if (character === "sber")    return <img  src={img_background_portal_sber}/>;
                            if (character === "eva")     return <img  src={img_background_portal_ava}/>;
                            if (character === "joy")     return <img  src={img_background_portal_joy}/>;
                        }

                    })()}

                </div>

                <Main device = {detectDevice()} posgame = {posGame} callbackL = {leftbuttondown} callbackR = {rightbuttondown}  buttonLDown = {buttonLDown} buttonRDown = {buttonRDown} commandfromback = {command}/>

            </>





        </AppStyled>

    );
});

export default App;