import React, {memo, useState} from 'react';
import TweenOne from 'rc-tween-one';
import BezierPlugin from 'rc-tween-one/lib/plugin/BezierPlugin';

import * as texts from '../data/texts'
import * as constant from "../const";

import * as data from "../images/FunnyCook/data.js"
import * as helper from "../helpers/helper"

import FunnyCook from "./FunnyCook"

// import * as constant from "../const"
TweenOne.plugins.push(BezierPlugin);




function Main(props) {

    const [game, setGame] = useState("all");


    // const character = props.character

    const width = window.innerWidth ;
    const height = window.innerHeight ;

    const getscale = () => {
        let device = props.device
        let rotate  = 0;
        if  (device === "touch")
        {

            return  width / 1920.0
            // device = "sberPortal";
        }

        switch (device) {
            case "sberBox":
                return 1
            case "sberPortal":
                return 0.666667

            default:
                return;
        }

    }
    const scale = getscale()
    let rotate  = 0;

   if (height > width) {
        rotate = 90
    }

    const posgame = props.posgame

    const top = 0
    const left = 0

    if   (game !== props.commandfromback)
    {
        switch (props.commandfromback) {

            case 'povar': {
                setGame('povar')
                break;

            }

        }

    }

    return (

        <>
            {game === 'all' && <>
             <FunnyCook scale = {0.3}
                                            rotate = {rotate}
                                            top = {150}
                                            left = {220}
                                            posgame = {posgame}
                                            callbackL = {props.callbackL}
                                            callbackR = {props.callbackR}
                                            buttonLDown ={props.buttonLDown}
                                            buttonRDown ={props.buttonRDown}
                                            commandfromback = {props.command}
            />
            </>}

            {game === 'povar' && <FunnyCook scale = {scale}
                        rotate = {rotate}
                        top = {top}
                        left = {left}
                        posgame = {posgame}
                        callbackL = {props.callbackL}
                        callbackR = {props.callbackR}
                        buttonLDown ={props.buttonLDown}
                        buttonRDown ={props.buttonRDown}
                        commandfromback = {props.commandfromback}
            /> }

        </>
    );
}

export default memo(Main)